  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  scroll-behavior: smooth;
  text-decoration: none;
}

iframe {
  width: 100%;
  height: 320vh;
  margin-top: -185vh;
  
}

body, html, #root{
  width: 100%;
